@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.BrandLogo{height:64px;padding:5px 0 5px 25px}.BrandLogo .BrandLogoAsset{height:100%}

.navbar{display:flex;justify-content:space-between;height:74px;box-shadow:0 0 1px #b3b3b3}.navbar .navbar-brand{height:100%}.navbar .navbar-actions{height:100%}

.navbar-actions-container{display:flex;height:100%}.navbar-actions-container>div{display:grid;place-content:center;height:100%;margin-left:5px;margin-right:5px;transition-duration:0.5s;cursor:pointer}.navbar-actions-container>div .action-item{border-radius:30px;color:#000;padding:10px;padding-left:30px;padding-right:30px;transition-duration:0.5s}.navbar-actions-container>div .action-item:hover{box-shadow:0 0 5px #2f2f2f}.navbar-actions-container>div>a>.dark-action-item{border-radius:30px;background:black;color:#fff;padding:10px;padding-left:30px;padding-right:30px;transition-duration:0.5s}.navbar-actions-container>div>a>.dark-action-item:hover{box-shadow:0 0 5px #2f2f2f}.navbar-actions-container>div>a:active,.navbar-actions-container>div>a:focus{text-transform:none}.navbar-actions-container .dark-action-item{border-radius:30px;background:black;padding:10px;padding-left:30px;padding-right:30px;transition-duration:0.5s;color:#fff}.navbar-actions-container .dark-action-item:hover{box-shadow:0 0 5px #2f2f2f}@media only screen and (max-width: 600px){.navbar-actions-container{display:none}}

